@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

form {
    border-radius: 10rem;
    background-color: var(--background-secondary-color);
    display: flex;
    align-items: center;
    padding: 0.8rem 1rem;

    align-content: space-between;
    grid-column: 4 / span 6;
    width: 100%;
}

input {
    font: var(--card-font);
    color: var(--main-font-color);
    background-color: transparent;
    border-style: none;
    width: 100%;
    
}

input:focus {
    outline: none;
}

button {
    background-image: url(/letras-redux/static/media/magnifier-glass.af3b13ac.svg);
    background-size: contain;
    background-repeat: no-repeat;
    background-color: transparent;
    border: none;
    width: 15px;
    height: 15px;
    
}

button:hover {
    cursor: pointer;
}


.search-results .search-field {
    grid-row-start: 1;
    margin: auto;
}

.wrapper.search-results {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.cards-container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    grid-column: span 12;
    grid-row-gap: 1%;
    row-gap: 1%;
    grid-column-gap: 2%;
    -webkit-column-gap: 2%;
            column-gap: 2%;
    background-color: var(--background-secondary-color);
    border-radius: 16px;
    padding: 10% 5%;
    margin: 5%
}

.book-card {
    display: grid;
    grid-column: span 3;
    align-items: center;
    grid-template-rows: 200px auto auto auto;
    padding: 1%;
    max-width: 200px;
    width: 100%;
    transition: all 0.2s linear;
}

.cards-container a {
    display: contents;
    text-decoration: none;
}

.book-card .cover {
    overflow: hidden;
    margin-bottom: 5%;
    width: 100%;
    height: 100%;
    justify-self: center;
    text-align: center;

}

.book-card .cover img {
    height: 100%;
    object-fit: cover;  
    border-radius: 5px;
}

.book-card .info {
    font: var(--card-font);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.book-card p {
    font: var(--card-font);
    align-self: start;
    margin-bottom: 5%;
    color: var(--secondary-font-color);
}

.book-card h2 {
    font: var(--card-font);
    align-self: start;
    text-align: center;
    color: var(--main-font-color);
}

.book-card:hover {
    border-radius: 9px;
    background: #ffffff;
    box-shadow: 12px 12px 24px #f0f0f0, -12px -12px 24px #ffffff;
    transform: scale(1.1);
}

.search-results .logo {
    grid-column: span 3;
    width: 100%;
    margin: 5%;
}

.search-results .logo img {
    width: 100%;
    height: 100%; 
}

.search-results .logo a {
    display: contents; 
}
.single-page-book .search-field {
    grid-row-start: 1;
    margin: auto;
}

.single-page-book-content {
    width: 100%;
    display: grid;
    grid-column: span 12;
    grid-template-columns: repeat(12, 1fr);
    grid-row-gap: 5%;
    row-gap: 5%;
    grid-column-gap: 2%;
    -webkit-column-gap: 2%;
            column-gap: 2%;
    background-color: var(--background-secondary-color);
    border-radius: 16px;
    padding: 10% 5%;
    margin: 5%    
}

.single-page-book-content .cover {
    grid-column: span 4;
}

.single-page-book-content .cover img{
    width: 100%;
    border-radius: 5px;

}


.single-page-book-content .info {
    grid-column: span 8;
    font: var(--card-font);
    color: var(--main-font-color);

}

.single-page-book-content .info h1 {
    margin-bottom: 3%;
}

.single-page-book-content .info p {
    line-height: 1.2rem;
    margin-bottom: 3%;
}


.single-page-book-content .info .button {
    background-color: var(--background-color);
    border-radius: 50px;
    color: black;
    padding: 2%;
    text-align: center;

}
.single-page-book-content .info  a{
    color: black;
    text-decoration: none;
    height: 100%;
}


.single-page-book .logo {
    grid-column: span 3;
    width: 100%;
    margin: 5%;
}

.single-page-book .logo img {
    width: 100%;
    height: 100%; 
}

.single-page-book .logo a {
    display: contents; 
}



:root {
  --background-color: #FFF2AD;
  --main-font-color: #414141;
  --secondary-font-color: #959595;
  --placeholder-font-color: #C4C4C4;
  --background-secondary-color: #FFFFFF;
  --main-font: 'Roboto Condensed', sans-serif;
  --secondary-font: 'Roboto', sans-serif;
  --searchbar-font: 0.75rem /1.7rem var(--secondary-font);
  --card-font: 1rem /1.2rem var(--main-font);
  --single-book-title-font: 3rem /1.7rem var(--main-font);
  --single-book-content-font: 1.25rem /1.7rem var(--main-font);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #FFF2AD;
  background-color: var(--background-color);
}

.App {

}

section {
  
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-column-gap: 2%;
  -webkit-column-gap: 2%;
          column-gap: 2%;
  /*max-width: 786px;*/
  margin: auto 5%;
  justify-items: center;

}


.home.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}


.home .logo {
  grid-column: 5 / span 4;
  margin-bottom: 10%;
  
}

.home .search-field {
  grid-column: 4 / span 6;
  width: 100%;
  
}


.loader {
  margin: 100px auto;
  font-size: 5px;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  text-indent: -9999em;
  -webkit-animation: load5 1.1s infinite ease;
  animation: load5 1.1s infinite ease;
  transform: translateZ(0);
  grid-column: span 12;
}
